/*Blur starts here*/
.blur-circle-purple {
  width: 629px;
  height: 618.452px;
  position: relative;
  display: flex;
  flex-shrink: 0;
  border-radius: 629px;
  background: rgba(123, 68, 177, 0.50);
  filter: blur(154.5px);
