/*Nav bar starts here*/
.navbar {
  overflow: hidden;
  text-align: center;
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.logo {
  position: relative;
  float: left; 
  height: 70px; 
  margin-right: 10px 20px; /* Add space between the logo and navigation links */
  border-radius: 10px;
}
.logo:hover {
  cursor: pointer;
}
.navbar a {
  display: inline-block;
  padding: 14px 16px;
  text-decoration: none;
  color: white;
}

.navbar a:hover {
  color: #3498db;
}

/*Nav bar ends here*/

